.custom-radio:checked::before {
  content: "";
  width: 10px;
  height: 10px;
  background: linear-gradient(to right, #e26e00, #fda106);
  border-radius: 50%;
  transition: all 0.2s ease;
}
.radio {
  appearance: none;
  -webkit-appearance: none;
  width: 19px;
  height: 19px;
  border: 2px solid #111;
  border-radius: 50%;
  display: grid;
  place-content: center;
  cursor: pointer;
  background: #fff;
}
.radio::before {
  content: "";
  width: 9px;
  height: 9px;
  border: 2px solid #111;
  border-radius: 50%;
}
.radio:checked::before {
  transform: scale(1);
}
.checkbox {
  appearance: none;
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #fff;
  display: grid;
  place-content: center;
  cursor: pointer;
}
.checkbox::before {
  content: "";
  font-size: 12px;
  font-weight: 700;
  color: #000;
  transform: scale(0);
  transition: transform 0.15s ease-in-out;
}
.checkbox:checked::before {
  transform: scale(1);
}
.notificationCheckbox {
  appearance: none;
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #fff;
