}
.notificationCheckbox:checked::before {
  transform: scale(1);
}
.price-range {
  position: absolute;
  width: 100%;
  appearance: none;
  background: transparent;
  cursor: pointer;
  pointer-events: none;
  bottom: 3px;
}
.price-range::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 9999px;
  background: #ffffff;
  border: 4px solid #003625;
  cursor: pointer;
  pointer-events: auto;
}
.price-range::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 9999px;
  background: #ffffff;
  border: 2px solid #003625;
  cursor: pointer;
  pointer-events: auto;
}
.price-input {
  width: 100%;
